// a doubly linked list of elements
export class singleLinkedList {

    constructor() {

        this.n = 0
        this.first = null
        this.last = null
    }

    reset() {
        this.n = 0
        this.first = null
        this.last = null        
    }

    length() { return this.n }

    // attach detach at the end of the list
    attach(p) {
        p.next = null
        if (this.last == null)  {
            this.first = this.last = p
        }
        else {
            this.last.next = p
            this.last = p 
        }
        this.n++
    }

    detach() {
        if (this.last==null) return null
        let p = this.last
        if (this.last==this.first)
            this.first = this.last = null
        else {
            this.last = this.first
            while (this.last.next) this.last = this.last.next
        }
        this.n--
        return p
    }

    // push and pop at the front of the list
    push(p) {
        p.next = this.first
        if (this.first == null) {
            this.first = this.last = p
        }
        else {
            this.first = p
        }
        this.n++
    }

    pop() {
        if (this.first == null) return null
        let p = this.first
        this.first = p.next
        p.next = null
        if (this.first == null) this.last = null
        this.n--
        return p
    }
}

export class doubleLinkedList {

    constructor() {

        this.n = 0
        this.first = null
        this.last = null
    }

    reset() {
        this.n = 0
        this.first = null
        this.last = null        
    }

    length() { return this.n }

    // attach detach at the end of the list
    attach(p) {
        p.next = null
        p.previous = this.last
        if (this.last == null)  {
            this.first = this.last = p
        }
        else {
            this.last.next = p
            this.last = p 
        }
        this.n++
    }

    detach() {
        if (this.last==null) return null
        let p = this.last
        this.last = p.previous
        p.previous = null
        if (this.last == null)
            this.first = null
        else 
            this.last.next = null
        this.n--
        return p
    }

    // push and pop at the front of the list
    push(p) {
        p.previous = null
        p.next = this.first
        if (this.first == null) {
            this.first = this.last = p
        }
        else {
            this.first.previous = p
            this.first = p
        }
        this.n++
    }

    pop() {
        if (this.first == null) return null
        let p = this.first
        this.first = p.next
        p.next = null
        if (this.first == null) 
            this.last = null
        else
            this.first.previous = null
        this.n--
        return p
    }

    // extract and insert somewhere in the list
    extract(p) {

        if (p==null) return
  
        if (p.previous) {
            p.previous.next = p.next
            if (p.next) 
                p.next.previous = p.previous
            else 
                this.last = p.previous
            p.next = p.previous = null
        }
        else {
            this.first = p.next
        }
        this.n--
    }

    insert(p, here) {

    }
}